"use client";
import { useState } from "react";
import { toast } from "sonner";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { Button } from "@/app/login/components/button";
import {
    Form,
    FormControl,
    FormDescription,
    FormField,
    FormItem,
    FormLabel,
    FormMessage,
} from "@/app/login/components/form";
import { Input } from "./components/input";
import { PasswordField } from "./components/password-input";

const formSchema = z.object({
    name_4580822079: z.string().min(1),
    name_6213865904: z.string(),
});

export default function MyForm() {
    const form = useForm<z.infer<typeof formSchema>>({
        resolver: zodResolver(formSchema),
    });

    function onSubmit(values: z.infer<typeof formSchema>) {
        try {
            console.log(values);
            toast(
                <pre className="mt-2 w-[300px] rounded-md bg-slate-950 p-4 text-sm">
                    <code className="text-white">{JSON.stringify(values, null, 2)}</code>
                </pre>
            );
        } catch (error) {
            console.error("Form submission error", error);
            toast.error("Failed to submit the form. Please try again.");
        }
    }

    return (
        <div className="flex items-center justify-center min-h-screen bg-gray-100 p-4">
            <Form {...form}>
                <form
                    onSubmit={form.handleSubmit(onSubmit)}
                    className="space-y-6 w-full max-w-sm bg-white shadow-md p-6 rounded-lg"
                >
                    <h2 className="text-xl font-semibold text-center">Login</h2>

                    {/* Username Field */}
                    <FormField
                        control={form.control}
                        name="name_4580822079"
                        render={({ field }) => (
                            <FormItem>
                                <FormLabel className="text-gray-700">Username</FormLabel>
                                <FormControl>
                                    <Input
                                        placeholder="Enter your username"
                                        className="w-full p-2 border border-gray-300 rounded-md focus:ring focus:ring-gray-200 text-sm"
                                        {...field}
                                    />
                                </FormControl>
                                <FormMessage />
                            </FormItem>
                        )}
                    />

                    {/* Password Field */}
                    <FormField
                        control={form.control}
                        name="name_6213865904"
                        render={({ field }) => (
                            <FormItem>
                                <FormLabel className="text-gray-700">Password</FormLabel>
                                <FormControl>
                                    <PasswordField
                                        placeholder="Enter your password"
                                        className="w-full p-2 border border-gray-300 rounded-md focus:ring focus:ring-gray-200 text-sm"
                                        {...field}
                                    />
                                </FormControl>
                                <FormMessage />
                            </FormItem>
                        )}
                    />

                    {/* Submit Button */}
                    <Button
                        type="submit"
                        className="w-full bg-black text-white rounded-md py-2 text-sm hover:bg-gray-900 transition"
                    >
                        Submit
                    </Button>
                </form>
            </Form>
        </div>
    );
}
